@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#background-video{background-image:url(/static/media/bg.eed1e659.jpg);background-size:cover;height:100vh;position:relative;z-index:1000}#background-video #logo{background-color:rgba(0,0,0,0.4);position:absolute;width:100%;height:100%;display:flex;align-items:center;justify-content:center}#background-video #logo img{width:50vh}

.navigation{background-color:#060b0e;font-family:'Open Sans';margin:0;padding:25px 0px;text-align:center;position:fixed;top:0px;width:100%;z-index:1020}.navigation li{display:inline-block}.navigation li a{color:#d3d3d3;display:block;font-size:13px;letter-spacing:1.3px;text-decoration:none;text-transform:uppercase;padding:6px 10px}

#landing #about-us-wrapper{background-color:#060b0e;padding:3em 1em;text-align:center}#landing #about-us-wrapper #about-us{margin:auto;max-width:920px}#landing #teams-wrapper{background-color:#060b0e;padding:3em 1em}#landing #teams-wrapper #team-list{display:flex;align-items:center;justify-content:center;flex-wrap:wrap}

#aboutusbg{background-color:#060b0e}#aboutusbg #aboutus{margin:auto;max-width:920px;padding:1em}

#aboutusbg{background-color:#060b0e}#aboutusbg #aboutus{margin:auto;max-width:920px;padding:1em}

#sponsorsbg{background-color:#060b0e}#sponsorsbg #sponsors{margin:auto;max-width:920px;padding:1em}#arc{height:256px}#orana{height:128px}#act{height:128px}

#faqbg{background-color:#060b0e}#faqbg #faq{margin:auto;max-width:920px;padding:1em}

#faqbg{background-color:#060b0e}#faqbg #faq{margin:auto;max-width:920px;padding:1em}#show-tables{border-collapse:collapse;color:black;width:100%}#show-tables th{text-align:left}#show-tables thead{background-color:#1abc9c;color:black}#show-tables thead th:nth-child(1){width:10%}#show-tables thead th:nth-child(2){width:40%}#show-tables thead th:nth-child(3){width:25%}#show-tables thead th:nth-child(4){width:25%}#show-tables tbody tr:nth-child(odd){background-color:#ffffff}#show-tables tbody tr:nth-child(even){background-color:#eeeeee}#show-tables th,#show-tables td{padding:0.5em 1em}

#footer{background-color:#060b0e;padding:3em 10em}

body{font-family:'Open Sans';color:white}.video{flex-grow:1;position:relative;height:calc(100vh - 80px)}.video .filter{background:rgba(0,0,0,0.1);position:absolute;display:flex;justify-content:center;width:100%;height:100%}.video .filter img{margin-top:13%;height:306px}.header{width:100%;height:308px;object-fit:cover;padding-top:50px}.menu{position:-webkit-sticky;position:sticky;top:0px}.title{color:#d3d3d3;font-family:'Noto Sans';font-size:24px;letter-spacing:2.4px;text-align:center}.team-item{border:none;background:none;padding-bottom:1em}.team-item .image{background-size:cover;height:396px;width:396px}.team-item .text{font-family:'Open Sans';font-size:18px;color:white;padding-top:1em}.center{text-align:center}a{color:#1abc9c}

